<template>
  <div class="wrap">
    <div class="login_wrap">
      <div class="header">
        <h1 class="show_title">{{title}}</h1>
      </div>
      <div class="content">
        <form action="/" method="post">
          <div class="div_user">
            {{user}}
            <el-input class="ipt_user" name="username" v-model="input_user" placeholder="请输入账号..."></el-input>
          </div>
          <div class="div_pwd">
            {{pwd}}
            <el-input
              class="ipt_pwd"
              name="passwd"
              v-model="input_pwd"
              placeholder="请输入密码..."
              show-password
            ></el-input>
          </div>
          <div class="div_btn">
            <el-button class="btn_sub" type="primary" round v-on:click="login">Login</el-button>
            <el-button class="btn_reg" round v-on:click="register">Register</el-button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { Loading } from "element-ui";

export default {
  data() {
    return {
      title: "学生管理系统",
      input_user: "",
      input_pwd: "",
      input_num: "",
      user: "账   号 ：",
      pwd: "密   码 ："
    };
  },
  created: function() {
  },
  methods: {
    login: function() {
      var user = this.input_user;
      var pwd = this.input_pwd;
      if (user.trim() == "" || user == undefined || user == null) {
        this.$message({
          showClose: true,
          message: "用户名为空,请重新输入!",
          type: "error"
        });
      } else if (pwd.trim() == "" || pwd == undefined || pwd == null) {
        this.$message({
          showClose: true,
          message: "密码为空,请重新输入!",
          type: "error"
        });
      } else if (user.length < 4 || user.length > 16) {
        this.$message({
          showClose: true,
          message: "用户名错误,用户名为4-16位,请重新输入!",
          type: "error"
        });
      } else if (pwd.length < 6 || pwd.length > 18) {
        this.$message({
          showClose: true,
          message: "密码错误，密码为6-18位,请重新输入!",
          type: "error"
        });
      } else {
        this.$http
          .post(
            "http://localhost:8088/user/login",
            {
              username: user,
              passwd: pwd
            },
            { emulateJSON: true }
          )
          .then(res => {
            if (res.data == true) {
              this.$message({
                showClose: true,
                message: "登录成功!",
                type: "success"
              });
              setTimeout(() => {
                this.$router.push({
                  name : "Index",
                  params:{
                    user : user
                  }

                });            
              }, 1000);
            } else if (res.data == false) {
              this.$message({
                showClose: true,
                message: "登录失败,请重新输入",
                type: "error"
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    register: function() {
       this.$router.push("/Register");
    }
  }
};
</script>

<style scoped>
body {
  margin: 0;
  padding: 0;
}
.wrap {
  width: 100%;
  height: 600px;
  margin-top: -50px;
  text-align: center;
  background-image: url("../assets/login_bg.jpg");
  background-size: 100% 600px;
}

.login_wrap {
  width: 362px;
  height: 400px;
  text-align: center;
  border-radius: 10px;
  margin: 0 auto;
  transform: translateY(20%);
  opacity: 92%;
  box-shadow: 0 0 30px #cac6c6;
  background-color: #fff;
}
.header {
  width: 100%;
  height: 100px;
  text-align: center;
}
.show_title {
  transform: translateY(50%);
  font-size: 40px;
}

.ipt_user,
.ipt_pwd,
.ipt_num {
  width: 200px;
  margin-top: 15px;
  margin-bottom: 20px;
}

.btn_sub,
.btn_reg {
  margin-top: 25px;
}
</style>



