<template>
  <div class="wrap">
    <div class="header">
      <el-input class="ipt" placeholder="请输入内容..." v-model="input" clearable></el-input>
      <el-button type="primary" plain v-on:click="query">查询</el-button>
    </div>
    <div class="content">
      <el-table :data="tableData" height="500" border style="width: 100%" slot="empty">
        <el-table-column prop="sno" label="学号" width="200"></el-table-column>
        <el-table-column prop="sname" label="姓名" width="180"></el-table-column>
        <el-table-column prop="sex" label="性别" width="150"></el-table-column>
        <el-table-column prop="age" label="年龄" width="150"></el-table-column>
        <el-table-column prop="sclass" label="班级" width="185"></el-table-column>
        <el-table-column prop="telphone" label="电话" width="200"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      input: "",
      tableData: []
    };
  },
  mounted() {
    var mytable = this.tableData;
    this.$http
      .post("http://localhost:8088/student/getStudentInfoList")
      .then(res => {
        var mydata = res.data.data;
        if (res.data.code == "1") {
          for (var i = 0; i < mydata.length; i++) {
            mytable.push(mydata[i]);
          }
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    query: function() {
      var params1 = this.input;
      this.tableData = [];
      var mytable = this.tableData;
      this.$http
        .post(
          "http://localhost:8088/student/getStudentInfoList",
          {
            params: params1
          },
          { emulateJSON: true }
        )
        .then(res => {
          var mydata = res.data.data;
          if (res.data.code == "1") {
            for (var i = 0; i < mydata.length; i++) {
              mytable.push(mydata[i]);
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 50px;
  text-align: right;
}
.ipt {
  width: 300px;
  height: 40px;
  margin-right: 15px;
}

.content {
  width: 100%;
  height: 450px;
}
</style>



