import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Register from '@/components/Register'
import Index from '@/components/Index'
import QueryData from '@/components/QueryData'
import InsertInfo from '@/components/InsertInfo'
import ModifyData from '@/components/ModifyData'
import NotFound from '@/components/NotFound'
import UpdateInfoPage from '@/components/UpdateInfoPage'




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    }, 
    {
      path: '/Register',
      name: 'Register',
      component: Register
    }, 
    {
      path: '/Index',
      name: 'Index',
      component: Index,
      // redirect:'/QueryData',
      children: [
        {
          path: '/QueryData',
          name: 'QueryData',
          component: QueryData,
        },
        {
          path: '/InsertInfo',
          name: 'InsertInfo',
          component: InsertInfo,
        }, 
        {
          path: '/ModifyData',
          name: 'ModifyData',
          component: ModifyData,
        },
        {
          path: '/UpdateInfoPage',
          name: 'UpdateInfoPage',
          component: UpdateInfoPage,
        },
        
      ]

    },
    {
      path: '/NotFound',
      name: 'NotFound',
      component: NotFound,
    },

  ]
})



