<template>
  <div class="wrap">
    <div class="header">
      <el-input class="ipt" placeholder="请输入学号..." v-model="ipt_sno" clearable></el-input>
      <el-input class="ipt" placeholder="请输入姓名..." v-model="ipt_sname" clearable></el-input>
      <el-input class="ipt" placeholder="请输入性别..." v-model="ipt_sex" clearable></el-input>
      <el-input class="ipt" placeholder="请输入年龄..." v-model="ipt_age" clearable></el-input>
      <el-input class="ipt" placeholder="请输入班级..." v-model="ipt_class" clearable></el-input>
      <el-input class="ipt" placeholder="请输入电话..." v-model="ipt_tel" clearable></el-input>

      <el-button type="primary" plain v-on:click="insert">录入</el-button>
    </div>
    <div class="content">
      <el-table :data="tableData" height="500" border style="width: 100%">
        <el-table-column prop="sno" label="学号" width="200"></el-table-column>
        <el-table-column prop="sname" label="姓名" width="180"></el-table-column>
        <el-table-column prop="sex" label="性别" width="150"></el-table-column>
        <el-table-column prop="age" label="年龄" width="150"></el-table-column>
        <el-table-column prop="sclass" label="班级" width="185"></el-table-column>
        <el-table-column prop="telphone" label="电话" width="200"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ipt_sno: "",
      ipt_sname: "",
      ipt_sex: "",
      ipt_age: null,
      ipt_class: "",
      ipt_tel: "",
      tableData: []
    };
  },
  mounted() {
    var mytable = this.tableData;
    this.$http
      .post("http://localhost:8088/student/getStudentInfoList")
      .then(res => {
        var mydata = res.data.data;
        if (res.data.code == "1") {
          for (var i = 0; i < mydata.length; i++) {
            mytable.push(mydata[i]);
          }
        }
      })
      .catch(err => {
        console.log("出错了!");
      });
  },
  methods: {
    insert: function() {
      var oSno = this.ipt_sno;
      var oSname = this.ipt_sname;
      var oSex = this.ipt_sex;
      var oAge = this.ipt_age;
      var oClass = this.ipt_class;
      var oTel = this.ipt_tel;
      // if (oSex != null || oSex.trim() != ""){
      //   oSex = oSex.trim();
      //   if(oSex != "男" || oSex != "女"){
      //      this.$message({
      //         showClose: true,
      //         message: "性别非法,性别为男或者女",
      //         type: "error"
      //      });
      //   }
      // }
      if (oSno == null || oSno.trim() == "") {
        this.$message({
          showClose: true,
          message: "学号不能为空,请重新输入!",
          type: "error"
        });
      } else if (oSname == null || oSname.trim() == "") {
        this.$message({
          showClose: true,
          message: "姓名不能为空,请重新输入!",
          type: "error"
        });
      }else if (oTel == null || oTel.trim() == "") {
        this.$message({
          showClose: true,
          message: "电话号码不能为空,电话号码为11位数字，请重新输入!",
          type: "error"
        });
      } else if (oAge < 0 || oAge > 100) {
        this.$message({
          showClose: true,
          message: "年龄录入错误,年龄大于0小于100，请重新输入!",
          type: "error"
        });
      }else {
        this.$http
          .post(
            "http://localhost:8088/student/insertStudentInfo",
            {
              sno: oSno,
              sname: oSname,
              sex: oSex,
              age: oAge,
              sclass: oClass,
              telphone: oTel
            }
            // { emulateJSON: true }
          )
          .then(res => {
            if (res.data.code == "0") {
              this.$message({
                showClose: true,
                message: "录入失败，请重试！",
                type: "error"
              });
            } else if (res.data.code == "1") {
              this.$message({
                type: "success",
                message: "录入成功!"
              });
              window.location.reload();
            } else {
              this.router.push("/NotFound");
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    }
  }
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 50px;
  text-align: right;
}
.ipt {
  width: 140px;
  height: 40px;
  margin-right: 15px;
}

.content {
  width: 100%;
  height: 450px;
}
</style>



