<template>
  <div class="wrap">
    <div class="title">
      <h1>学生信息</h1>
    </div>
    <div class="content">
      <el-table :data="tableData" style="width: 100%" max-height="450" border>
        <el-table-column label="学号" width="150">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.sno }}</span>
          </template>
        </el-table-column>

        <el-table-column label="姓名" width="150">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.sname }}</span>
          </template>
        </el-table-column>

        <el-table-column label="性别" width="120">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.sex }}</span>
          </template>
        </el-table-column>

        <el-table-column label="年龄" width="120">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.age }}</span>
          </template>
        </el-table-column>

        <el-table-column label="班级" width="150">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.sclass }}</span>
          </template>
        </el-table-column>

        <el-table-column label="电话" width="150">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.telphone }}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      reload: this.reload,
      tableData: []
    };
  },
  mounted() {
    var mytable = this.tableData;
    this.$http
      .post("http://localhost:8088/student/getStudentInfoList")
      .then(res => {
        var mydata = res.data.data;
        if (res.data.code == "1") {
          for (var i = 0; i < mydata.length; i++) {
            mytable.push(mydata[i]);
          }
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    handleEdit(index, row) {
      this.$router.push({
        name: "UpdateInfoPage",
        params: {
          sno: row.sno,
          sname: row.sname,
          sex: row.sex,
          age: row.age,
          sclass: row.sclass,
          telphone: row.telphone
        }
      });
    },
    handleDelete(index, row) {
      var data = row.sno;
      this.$confirm("是否要删除该条数据？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
        beforeClose: (action, instance, done) => {
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "删除中...";
            setTimeout(() => {
              this.$http
                .post(
                  "http://localhost:8088/student/deleteData",
                  {
                    param: data
                  },
                  { emulateJSON: true }
                )
                .then(res => {
                })
                .catch(err => {
                  console.log(err);
                });
              setTimeout(() => {
                instance.confirmButtonLoading = false;
              }, 300);
              done();
              // this.$router.push("/QueryData");
              window.location.reload();
            }, 1000);
          } else {
            done();
          }
        }
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>

<style scoped>
</style>



