<template  >
  <div class="wrap">
    <div class="content">
      <div class="title">
        <h1>用户注册</h1>
      </div>
      <div class="content_wrap">
        <div>
          账&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号：
          <el-input
            class="input_text"
            style="margin-top:50px;"
            placeholder="请输入账号..."
            v-model="username"
            clearable
          ></el-input>
        </div>
        <div>
          密&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码：
          <el-input
            class="input_text"
            placeholder="请输入密码..."
            v-model="passwd"
            clearable
            show-password
          ></el-input>
        </div>

        <div style="margin-bottom:50px;">
          确认密码：
          <el-input
            class="input_text"
            placeholder="重复密码..."
            v-model="re_passwd"
            clearable
            show-password
          ></el-input>
        </div>

        <div class="operation">
          <el-button type="primary" v-on:click="register">注册</el-button>
          <el-button type="info" v-on:click="cancle">退出</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      passwd: "",
      re_passwd: ""
    };
  },
  methods: {
    register: function() {
      var username = this.username;
      var passwd = this.passwd;
      var re_passwd = this.re_passwd;
      if (username.trim() == "" || username == undefined || username == null) {
        this.$message({
          showClose: true,
          message: "用户名为空,请重新输入!",
          type: "error"
        });
      } else if (passwd.trim() == "" || passwd == undefined || passwd == null) {
        this.$message({
          showClose: true,
          message: "密码为空,请重新输入!",
          type: "error"
        });
      } else if (
        re_passwd.trim() == "" ||
        re_passwd == undefined ||
        re_passwd == null
      ) {
        this.$message({
          showClose: true,
          message: "确认密码为空,请重新输入!",
          type: "error"
        });
      } else if (username.length < 4 || username.length > 16) {
        this.$message({
          showClose: true,
          message: "用户名为4-16位，请重新输入！",
          type: "error"
        });
      } else if (passwd.length < 6 || passwd.length > 18) {
        this.$message({
          showClose: true,
          message: "密码为6-18位，请重新输入！",
          type: "error"
        });
      } else if (passwd != re_passwd) {
        this.$message({
          showClose: true,
          message: "两次密码不一致，请重新输入！",
          type: "error"
        });
      } else {
        this.$http.post('http://localhost:8088/user/register',{
          username : username,
          passwd: passwd
        }).then((res)=>{
          if (res.data.code == "0") {
            this.$message({
              showClose: true,
              message: "注册失败！请重新注册。",
              type: "error"
            });
          }else if (res.data.code == "1") {
              this.$message({
                type: "success",
                message: "注册成功!"
              });
              setTimeout(() => {
                this.$router.push("/");
              }, 500);           
            }
        }).catch((err)=>{
            console.log(err);
        });
      }
    },
    cancle: function() {
      this.$message({
        type: "info",
        message: "已取消注册!"
      });
      this.$router.push("/");
    }
  }
};
</script>

<style scoped>

.title {
  width: 100%;
  height: 60px;
  transform: translateY(40%);
  background-color: #fff;
}

.content {
  width: 450px;
  height: 420px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 0 30px #cac6c6;
  background-color: #fff;
}

.operation {
  width: 380px;
  height: 60px;
  margin: 0 auto;
}
.input_text {
  width: 200px;
  height: 40px;
  margin-bottom: 20px;
}
</style>



