<template>
  <div class="wrap">
    <div class="title">
      <h1>学生信息修改</h1>
    </div>
    <div class="content">
      <div class="content_wrap">
        <div>
          学号：
          <el-input
            class="input_text"
            style="margin-top:15px;"
            placeholder="请输入内容"
            v-model="sno"
            clearable
            disabled
          ></el-input>
        </div>
        <div>
          姓名：
          <el-input class="input_text" placeholder="请输入内容" v-model="sname" clearable></el-input>
        </div>
        <div>
          性别 ：
          <el-input class="input_text" placeholder="请输入内容" v-model="sex" clearable></el-input>
        </div>
        <div>
          年龄 ：
          <el-input class="input_text" placeholder="请输入内容" v-model="age" clearable></el-input>
        </div>
        <div>
          班级 ：
          <el-input class="input_text" placeholder="请输入内容" v-model="sclass" clearable></el-input>
        </div>
        <div>
          电话 ：
          <el-input class="input_text" placeholder="请输入内容" v-model="telphone" clearable></el-input>
        </div>
        <div class="operation">
          <el-button type="primary" v-on:click="modify">修改</el-button>
          <el-button type="info" v-on:click="cancle">取消</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sno: this.$route.params.sno,
      sname: this.$route.params.sname,
      sex: this.$route.params.sex,
      age: this.$route.params.age,
      sclass: this.$route.params.sclass,
      telphone: this.$route.params.telphone
    };
  },
  methods: {
    modify: function() {
      this.$http
        .post("http://localhost:8088/student/updateInfo", {
          sno: this.sno,
          sname: this.sname,
          sex: this.sex,
          age: this.age,
          sclass: this.sclass,
          telphone: this.telphone
        })
        .then(res => {
          if (res.data.code == "1") {
            this.$message({
              type: "success",
              message: "修改成功!"
            });
            this.$router.push("/ModifyData");
          }
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: "修改失败，请重试!"
          });
          console.log(err);
        });
    },
    cancle: function() {
      this.$message({
        type: "info",
        message: "已取消修改!"
      });
      this.$router.push("/ModifyData");
    }
  }
};
</script>

<style scoped>
.wrap {
  width: 100%;
  height: 500px;
  text-align: center;
}

.title {
  width: 100%;
  height: 60px;
  background-color: #fff;
}

.content {
  width: 450px;
  height: 420px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 0 30px #cac6c6;
  background-color: #fff;
}

.operation {
  width: 380px;
  height: 60px;
  margin: 0 auto;
}
.input_text {
  width: 200px;
  height: 40px;
  margin-bottom: 15px;
}
</style>



