<template>
  <div class="wrap">
    <div class="header">
      <div class="logo_wrap">
        <img class="header_logo" src="../assets/logo.png" />
      </div>
      <div class="center">
        <h1 class="title">学生后台管理系统</h1>
      </div>

      <div class="header_right">
        <div class="show_name">
          <h3 style="color:white">你好,{{username}}</h3>
        </div>
        <div class="show_date">
          <span id="date" style="color:white;width:100px;">{{date}}</span>
        </div>
      </div>
      
    </div>
    <div class="content">
      <template>
        <div style="min-height:600px">
          <div style="margin-top:5px">
            <el-row :gutter="10">
              <el-col :xs="4" :sm="4" :md="4" :lg="4">
                <div>
                  <el-menu
                    default-active="1"
                    class="el-menu-vertical-demo"
                    style="min-height:600px"
                    @select="handleSelect"
                  >
                    <el-menu-item index="1">学生信息查询</el-menu-item>
                    <el-menu-item index="2">学生信息录入</el-menu-item>
                    <el-menu-item index="3">学生信息修改</el-menu-item>
                    <el-menu-item index="4">退出系统</el-menu-item>
                  </el-menu>
                </div>
              </el-col>
              <el-col :xs="20" :sm="20" :md="20" :lg="20">
                <div style="margin-top:10px">
                  <router-view></router-view>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: this.$route.params.user,
      date: new Date(),
      tabPosition: "left",
      searchCriteria: "",
      breadcrumbItems: ["学生信息查询"],
      tableData: []
    };
  },
  mounted: 
    function getDate() {
    this.$router.push("/QueryData");  
    let myThis = this;
    myThis.date = new Date().toLocaleString();
    this.timer = setInterval(function() {
      myThis.date = new Date().toLocaleString();
    });
  },
  
  methods: {

    handleSelect(flag, keyPath) {
      switch (flag) {
        case "1":
          this.$router.push("/QueryData");
          this.breadcrumbItems = ["学生信息查询"];
          break;
        case "2":
          this.$router.push("/InsertInfo");
          this.breadcrumbItems = ["学生信息录入"];
          break;
        case "3":
          this.$router.push("/ModifyData");
          this.breadcrumbItems = ["学生信息修改"];
          break;
        case "4":
          this.$confirm("是否退出当前系统？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
            center: true,
            beforeClose: (action, instance, done) => {
              if (action === "confirm") {
                instance.confirmButtonLoading = true;
                instance.confirmButtonText = "执行中...";
                setTimeout(() => {
                  done();
                  this.$router.push("/");
                  setTimeout(() => {
                    instance.confirmButtonLoading = false;
                  }, 300);
                }, 1000);
              } else {
                done();
              }
            }
          })
            .then(() => {
              this.$message({
                type: "success",
                message: "你已退出该系统,欢迎下次使用!"
              });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消"
              });
            });
          this.breadcrumbItems = ["退出系统"];
          break;
      }
    }
  }
};
</script>

<style scoped>
.header {
  position: relative;
  min-width: 1100px;
  width: 100%;
  height: 100px;
  margin-top: -50px;
  background-color: rgb(33, 86, 163);
}
.logo_wrap {
  position: absolute;
  width: 360px;
  height: 90px;
}

.header_logo {
  width: 330px;
  height: 75px;
  float: left;
  margin-top: 10px;
  margin-left: 20px;
}

.center {
  position: absolute;
  width: 500px;
  height: 80px;
  margin-left: 400px;
  margin-right: 10px;
  margin-top: 5px;
}

.title {
  font-size: 35px;
  color: white;
}

.header_right {
  position: relative;
  width: 300px;
  height: 80px;
  text-align: center;
  margin-right: 10px;
  margin-top: 10px;
  float: right;
}
.content {
  position: absolute;
  width: 95%;
  height: 500px;
}

.location {
  width: 100%;
  height: 30px;
  text-align: left;
  margin-top: 3px;
}
</style>



